.App {
  text-align: center;
}

.card-container {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}

.card-grid {
  display: grid;
  grid-gap: 20px;
  grid-auto-flow: row dense;
  max-width: 1280px;
}

.map {
  max-width: 260px;
}

.country-map-container {
  background-color: #000;
  padding: 20px 20px 0 20px;
}

.flag {
  height: 40px;
}

.country-name {
  color: #fff;
}

.country-flag-name {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.country-name {
  display: flex;
  align-items: center;
}

.country-owner-container {
  background-color: #eee;
}

.country-owner {
  height: 40px;
  width: 100%;
  border-radius: 0 !important;
  text-transform: initial !important;
  font-size: 16px !important;
}

.country-owner-without-url {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.country-description-title,
.country-description {
  font-size: 14px;
  font-weight: 400;
  width: 110px;
  text-align: left;
}

.country-description-title {
  color: #999;
  display: inline-block;
  margin: 10px 0 0 20px;
}

.country-description {
  margin-left: 20px;
}

.country-description-container {
  display: flex;
  flex-direction: row;
}

.country-divider {
  margin-top: 10px !important;
}

.buy-button-container {
  padding: 10px 20px 0 20px;
}

.buy-button {
  width: 100%;
}

.owned-by-you {
  color: #999;
}

.next-price {
  color: #999;
  margin: 10px 0;
}

.tool-bar {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}

.set-player-card {
  display: flex;
  flex-direction: column;
  padding: 0 30px 30px 30px;
}

.player-name, .player-url {
  width: 100%;
}

.set-player-button {
  width: 200px;
}

.set-player {
  text-transform: initial !important;
}

.charity-fund,
.my-countries,
.how-to-play,
.my-logs {
  margin-right: 30px !important;
}

.footer-container {
  color: #999;
}

.footer-container a {
  color: #999;
}

.tool-bar-buttons {
  display: flex;
}

.my-countries-card {
  min-width: 375px;
  padding: 0 20px 20px;
}

.my-countries-description {
  text-align: center;
}

@media (max-width: 768px) { 
  .tool-bar {
    flex-direction: column;
    padding: 20px 0;
  }

  .tool-bar-buttons {
    flex-direction: column;
  }

  .tool-bar-buttons a,
  .tool-bar-buttons button {
    min-width: 320px;
  }

  .charity-fund,
  .how-to-play,
  .set-player,
  .my-logs,
  .my-countries {
    margin-top: 10px !important;
    margin-right: 0 !important;
    min-width: 320px;
  }
}

.my-logs {
  display: none !important;
}

@media (min-width: 768px) {  
  .card-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {  
  .card-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1280px) {  
  .card-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.main-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 20px;
}

.card {
  background-color: #343a40 !important;
  margin: 20px;
  padding: 10px;
  min-height: 260px;
}

.small-card {
  width: 400px;
}

.big-card {
  width: 500px;
}

.card-title {
  font-size: 0.8125rem;
  font-weight: 500;
  margin: 15px 0;
  text-transform: uppercase; 
}

.card-title-divider {
  height: 2px;
  border-width: 0;
  background-color: #e6ae49;
}

.left {
  align-self: flex-start;
}

.right {
  margin-left: auto;
}

.buy-box {
  background-color: #f8f9fa;
  color: #495057;
  border-radius: 5px;
  display: flex;
  border: 1px solid #333;
  height: 40px;
}

.buy-icon {
  width: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e9ecef;
  border-radius: 5px 0 0 5px;
}

.buy-amount {
  padding: 0 10px;
  margin: 0;
  width: 60%;
  font-size: 16px;
}

.buy-price {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e9ecef;
  color: #495057;
  border-radius: 0 5px 5px 0;
}

#buy-button, #claim-button {
  margin-top: 20px;
  background-color: #f000f0 !important;
  width: 100%;
}

.total-qtum {
  font-size: 4.5rem;
  line-height: 1.2;
  text-shadow: rgb(43, 0, 43) 0px 0px 5px, rgb(204, 0, 204) 0px 0px 20px, rgb(255, 0, 255) 0px 0px 10px;
  margin-top: 40px;
}

.countdown-timer {
  font-size: 34px;
  font-weight: 300;
  line-height: 1.2;
}

.logo {
  margin: 0 0 -10px 10px;
  width: 70px;
  line-height: 1.2;
}

.table-cell {
  color: #fff !important;
}

.round-info-container {
  padding: 0 20px;
}

.round-info {
  display: flex;
  justify-content: space-between;
  margin: 10px 0 0;
}

.keys-container {
  display: flex;
  justify-content: space-around;
  margin: 10px 0 0;
}

.contract-link {
  color: #fff;
}

.appbar-info {
  min-width: 170px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.trademark {
  text-shadow: rgb(43, 0, 43) 0px 0px 5px, rgb(204, 0, 204) 0px 0px 20px, rgb(255, 0, 255) 0px 0px 10px;
}

@media (max-width: 768px) { 
  .trademark {
    margin-bottom: 15px;
  }

  .main-container {
    flex-direction: column;
    align-items: center;
  }

  .card { 
    margin: 20px 0;
  }

  .small-card, .big-card {
    width: 90%;
  }
}